/* Product page */
.page-template.page-product-template {
    margin-top: 0;
    /* Related product */
    .secion-related-products .section-header {
        margin: 0 0 20px;
        .section-heading {
            margin: 0;
        }
    }
}  
/* Info - Image */ 
.section-product-template .section-body {
    margin-bottom: 40px;   
    @media (min-width: 992px) { 
        margin-bottom: 60px;  
        padding: 30px; 
        background: var(--second-bg-color);
    }
}
.product-media { 
    .product-media_thumbnails {
        margin-top: 16px;
        margin-bottom: 16px; 
        @media (min-width: 992px) {
            margin-top: 24px;
            margin-bottom: 0; 
        }
    }
    .product-media_main_sold-out {
        display: none;
    }
    .product-sold-out {
        display: block;
        position: relative;
        .product-media_main_sold-out {
            display: block;
            @include ab(50%, 50%, n, n);
            background: rgba(30, 30, 30, 0.5);
            border-radius: 6px;
            color: #ffffff;
            text-transform: uppercase;
            font-size: 16px;
            padding: 8px 15px;
            font-weight: var(--font-bold);
            transform: translate(-50%, -50%);
            @media (min-width: 750px) {
                font-size: 24px;
                padding-left: 20px;
                padding-right: 20px;
            }
        }
    }
}
.product-info {  
    .product-info_details {
        @media (max-width: 991px) {
            padding-top: 20px;
            background: var(--second-bg-color);
        }
    }
    .product-info_media .slider-wrapper {
        position: relative;
        .slider-prev-button {
            left: 15px;
        }
        .slider-next-button {
            right: 15px;
        }
    }
    .product-media_thumbnails {
        .media-item {
            border-radius: var(--input-radius);
            img {
                border-radius: var(--input-radius); 
                opacity: 0.3;
            }
            &.tns-nav-active img {
                opacity: 1;
            } 
        }
    }  
    .product-info_title {
        margin-bottom: 15px;
    }
    .porduct-details { 
        height: 100%;
    }
    .product-details_vendor {
        text-transform: uppercase;
        font-size: calc(var(--body-font-size) + 2px);
        margin-bottom: 2px;
        display: inline-block; 
        color: var(--body-color-2);
    }
    .product-details_title {
        line-height: 1.3;
        margin-bottom: 4px; 
        font-weight: var(--font-body-weight);
    } 
    .product-info_reviews {
        display: block;
        @media (min-width: 370px) {
            display: flex;
        }
        >div:not(:last-child) {
            margin-bottom: 5px;
            @media (min-width: 370px) {
                @include border-inline-end(1px, solid, var(--body-border-color));
                @include padding-end(var(--gutter-xxs));
                @include margin-end(var(--gutter-xxs)); 
                margin-bottom: 0;
            }
        } 
    }
    .product-details_sku {
        color: var(--text-miscellaneous);
        text-transform: uppercase; 
    }
    .product-details_price {
        flex-wrap: wrap;
        margin-bottom: var(--gutter-xs); 
        @media (min-width: 992px) {
            margin-bottom: 28px; 
        }
    }
    .product-details_price-inner {
        font-size: calc(var(--product-card-price-fs) * 2);
        color: var(--price-color);
        display: flex; 
        align-items: center; 
        flex-wrap: wrap;
        font-weight: var(--font-bold);
        line-height: 1.1;
        .price-sale {
            color: var(--price-sale-color);
            flex: 100%;
            @media (min-width: 750px) {
                @include margin-end(11px);
                flex: auto;
            }
        }
        .price-compare {
            color: var(--price-compare-color);
            font-weight: var(--font-body-weight); 
            font-size: var(--product-card-price-compare-fs);
        }
    }
    .product-details_price-save {
        margin-top: 8px;
        @media (min-width: 750px) {
            margin-top: 0;
        }
    }
    .label-save-price {
        font-size: var(--body-font-size);
        color: var(--main-color);
        font-weight: var(--font-bold);
        @include margin-start(8px);
    }
    .product-details_price-title {
        margin-bottom: 2px;
    } 
    .product-details_inventory {
        color: var(--body-link);
        margin-bottom: 28px; 
        svg {
            @include margin-end(10px);
        }
        button {
            font-size: var(--body-font-size);
            @include font-body;
            color: var(--body-link);
            text-decoration: underline;
            &:hover {
                @media (hover: hover) {
                    color: var(--body-link-hover);
                }
            }
        }
        &[data-status="low-stock"] {
            svg {
                color: #ed462f;
            }
            .inventory {
                display: none;
                &.inventory_low-stock { 
                    display: block; 
                }
            } 
        }
        &[data-status="in-stock"] {
            svg {
                color: #12a05c; 
            }
            .inventory {
                display: none;
                &.inventory_in-stock {
                    display: block; 
                }
            }
        } 
        &[data-status="out-stock"] {
            svg {
                color: #ed462f;
            }
            .inventory {
                display: none;
                &.inventory_out-stock {
                    display: block; 
                }
            } 
            .product-details_inventory_store {
                display: none;
            }
        }
        &[data-status="coming-stock"] {
            svg {
                color: #12a05c; 
            }
            .inventory {
                display: none;
                &.inventory_coming-stock { 
                    display: block; 
                }
            } 
        }
        &[data-status="items-stock"] {
            svg {
                color: #12a05c; 
            }
            .inventory {
                display: none;
                &.inventory_items-stock {
                    display: block;  
                }
            }
        }
    }
    .product-detaitls_form {
        margin-bottom: 17px;  
        @media (min-width: 750px) {
            margin-bottom: var(--gutter-sm); 
        }
    }
    .product-form_quantity label {
        margin: 0 0 7px;
        font-weight: var(--font-bold);
        @media (min-width: 750px) {
            font-size: calc(var(--body-font-size) + 2px);
            flex: 0 0 100%;
        } 
    }
    .product-form_quantity_wrapper {
        @media (min-width: 750px) {
            display: flex;
        }
    }
    .btn { 
        flex: 0 0 100%; 
        position: relative;
        margin-top: 16px;
        font-weight: var(--font-bold);
        text-transform: uppercase;
        width: 100%;
        @media (min-width: 750px) {
            width: auto;
            margin-top: 0;
            flex: 1;
            @include margin-start(12px);
        }
        .svg-loading {
            display: none; 
            @include ab(50%, 50%, n, n);
            transform: translate(-50%, -50%);
            circle {
                stroke: var(--btn1-hover-cl); 
            }
        }
        &.pending {
            >span {
                color: transparent; 
            }
            .svg-loading {
                display: block; 
            } 
        }
        &[disabled] {
            opacity: 0.6;
            cursor: not-allowed;
            &:hover {
                @media (hover:hover) {
                    color: var(--btn1-cl);
                    background: var(--btn1-bg-cl);
                    border: var(--btn1-border-width) solid var(--btn1-border-cl); 
                } 
            }
        }  
    }
    .product-details_more-payment {
        margin-bottom: var(--gutter-xs);
        display: block;
        @media (min-width: 992px) {
            margin-bottom: var(--gutter-md);
        }
        span {
            display: block;
            margin-bottom: 4px;
        }
        ul li {
            margin: 5px; 
            display: flex;
            &:first-child {
                @include margin-start(0);
            }
            &:last-child {
                @include margin-end(0);
            }
        } 
    }
    .product-form_dynamic-checkout {
        margin-top: 16px; 
        @media (min-width: 992px) {
            margin-top: 12px; 
        }
        .shopify-payment-button__button {
            @include point-cursor;
            display: block;
            padding: 12px 10px;  
            text-align: center; 
            box-shadow: none;
            text-decoration: none;
            font-size: var(--btn2-fs);
            font-weight: var(--font-bold); 
            border-radius: var(--btn1-radius);  
            line-height: inherit; 
            @include font-body;  
            @include transition;
            color: var(--btn2-cl);
            background: var(--btn2-bg-cl);
            border: var(--btn2-border-width) solid var(--btn2-border-cl); 
            text-transform: uppercase;
            animation: none;
            &:hover {
                @media (hover: hover) {
                    text-decoration: none;
                    color: var(--btn2-hover-cl);
                    background: var(--btn2-hover-bg-cl) !important;
                    border-color: var(--btn2-hover-border-cl);
                }   
            }  
            &[disabled] {
                opacity: 0.6;
                cursor: not-allowed;
                &:hover {
                    @media (hover:hover) {
                        color: var(--btn2-cl);
                        background: var(--btn2-bg-cl) !important;
                        border: var(--btn2-border-width) solid var(--btn2-border-cl); 
                    } 
                }
            }
        }
        .shopify-payment-button__button--hidden {
            display: none;
        }
    }
}
/* End info - image */
/* Quantity */
.product-form_quantity {
    .select-group {
        position: relative;
        max-width: 108px; 
        width: 100%;
        select {
            width: 100%;
            height: 48px;
            text-align: start; 
            @include padding-end(42px);
            @media (min-width: 750px) {
                height: 100%;
            }
        }
        svg {
            height: 100%;
            @include ab(50%, n, n, 11px);
            transform: translate(0, -50%);
            @include point-event;
        }
        &:after {
            content: "";
            @include ab(50%, n, n, 40px);
            width: 1px;
            height: 25px;
            background: var(--body-border-color);
            transform: translate(0, -50%);
        }
    }
}
/* End quantity */
/* Notify */
.contact-form {
    margin-bottom: 14px;
    label {
        font-size: calc(var(--body-font-size) + 2px);
        margin-bottom: 5px;
        p {
            margin: 0;
        }
    }
    .product-subscribe_form .product-subscribe_wrapper {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        input {
            flex: 1;
            height: auto;
            display: block;
            width: 100%;
        }
        .btn {
            min-width: 203px;
            flex: 0 0 auto;
        }
    }
    .product-subscribe_message {
        display: none;
        align-items: center;
        .product-subscribe_message_icon {
            flex: 0 0 64px;
            width: 64px;
            border-radius: 50%;
            height: 64px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba(#12a05c, 0.1);
            @include margin-end(8px);
            svg {
                color: #12a05c;
            }
        }
        .product-subscribe_message_heading {
            font-size: calc(var(--body-font-size) + 4px);
        }
        .product-subscribe_message_subheading {
            color: var(--body-color-2);
        }
    }
    .posted-successfully {
        .product-subscribe_form {
            display: none;
        }
        .product-subscribe_message {
            display: flex;
        }
    }
}
/* End notify */
/* Swatch color - size */
.swatch-group {
    margin-bottom: 17px;
    @media (min-width: 992px) {
        margin-bottom: 23px; 
    }
    .swatch-group_title {
        margin-bottom: 5px;
        font-size: calc(var(--body-font-size) + 2px);
        font-weight: var(--font-bold);
    }
    .swatch-group_selected-option {
        font-size: var(--body-font-size);
        font-weight: var(--font-body-weight);
    }
    .select-group {
        position: relative; 
        width: 100%;
        @media (min-width: 750px) {
            min-width: 50%;
            width: auto;
        }
        select {
            width: 100%; 
            min-height: 48px;
            height: auto;
            @include padding-end(50px);
            display: block;
            box-sizing: border-box;
            white-space: normal;
            word-wrap: break-word;
        }
        svg {
            height: 100%;
            pointer-events: none;  
            @include ab(50%, n, n, 11px);
            transform: translate(0, -50%);
        }
        &:after {
            content: "";
            @include ab(50%, n, n, 40px);
            width: 1px;
            height: 25px;
            background: var(--body-border-color);
            transform: translate(0, -50%);
        }
    }
	.swatch-group_inner {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
        &:not(.swatch-style-select) {
            margin: -6px -6px 0;
        }
        .swatch-group_wrapper {
            padding: 6px 6px 0;
        }
        p {
            display: block;
            font-size: var(--body-font-size);
            margin: 7px 0 0;
        }
		.input-group {
			position: relative;
			display: flex;
			padding: 6px; 
			input {
				@include ab(0, 0, 0, 0);
				padding: 0;
				width: 100%;
				height: 100%;
				border: none;
				background: transparent; 
				&:checked ~ label:before {
					border-color: var(--main-color);
				}
				&[disabled] {
					cursor: not-allowed;
					&:after {
						content: "";
                        position: absolute;
						width: 100%;
						height: 100%;
						z-index: 1;
						background: linear-gradient(to top left, transparent calc(50% - 1px), #d4d4d4, transparent calc(50% + 1px));
					}
				}
			}
		}
		label {
			width: 34px;
			height: 34px;
			border-radius: var(--input-radius); 
			margin: 0; 
            font-size: calc(var(--body-font-size) + 2px);
			display: flex;
			align-items: center;
			justify-content: center; 
            @include point-cursor;
            span {
                width: 100%;
            }
			img {
				object-fit: cover;
			}
			&:before {
				content: "";
				@include ab(0, 0, 0, 0);
				border: 2px solid var(--body-border-color); 
				border-radius: var(--input-radius); 
			}
		} 
		&.swatch-style-color label {
			background-color: currentColor;
			&[data-custom-color="White"] {
				border: 1px solid var(--body-border-color);
			} 
		}
		&.swatch-style-button label {
			min-width: 34px;
			min-height: 34px;
			height: auto;
			width: auto;
            padding: 2px;
		}
	}
    &.swatch-group--larger .swatch-group_inner {
        &:not(.swatch-style-select) {
            margin: -15px -15px 0;
        }
        .swatch-group_wrapper {
            padding: 15px 15px 0;
        }
        label {
            width: 68px;
            height: 68px;
        }
        p {
            font-size: calc(var(--body-font-size) + 2px);
        }
        &.swatch-style-button label {
            min-width: 68px;
            min-height: 68px;
        }
    }
}
/* Swatch color - size */  
/* Tab description */
.product-tabs {
    margin-bottom: 60px; 
    .nav-controls {
        display: block;
        overflow-y: hidden;
        overflow-x: auto;
        white-space: nowrap; 
        li {
            color: var(--body-link);
            padding: 17px 15px; 
            background-color: #d4d4d4;
            text-transform: uppercase;
            font-size: calc(var(--body-font-size) + 2px);
            font-weight: var(--font-bold);  
            @include point-cursor;
            border-top: 2px solid transparent;  
            text-align: center; 
            @media (min-width: 1000px) {
                padding: 17px 55px; 
            }
            &:not(:last-child) {
                @include margin-end(4px); 
            }
            &.active {
                background-color: var(--second-bg-color); 
                border-top: 2px solid var(--main-color);
            }
        }
    }
    .tab-contents {
        background-color: var(--second-bg-color);
        padding: 22px 30px 27px; 
        position: relative; 
        .tab-item {
            p, div { 
                margin: 0;
                &:not(:last-child) {
                    margin-bottom: var(--gutter-xs); 
                }
            } 
            &:not(.active) {
                @include ab(0, 0, 0, 0);
                @include visibility-hidden;  
                height: 0;
            } 
        }
        
    }  
    .video {
        display: block;
        position: relative;
        width: 100%;
        height: 0;
        padding-bottom: 56.25%;
        iframe {
            position: absolute; 
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        } 
    }
}
/* End tab description */
.section-footer {
    .section-footer_item {
        margin-bottom: 55px; 
        @media (max-width: 991px) {
            background: var(--second-bg-color);
            padding: 15px;
            &:not(:last-child) {
                margin-bottom: 16px;
            }
        }
    }
    collapse-panel {
        @media (min-width: 992px) {
            cursor: default;
        }
        &[open] .bold-angle-down {
            transform: rotate(180deg);
        }
        .bold-angle-down {
            @include transition;
        }
    }
    collapse-panel-header {
        text-transform: uppercase;
        @media (max-width: 991px) {
            font-size: calc(var(--body-font-size) + 2px);
        }
        @media (min-width: 992px) {
            margin-bottom: 14px;
            font-weight: 300; 
        }
        b {
            font-weight: inherit;
            @media (min-width: 992px) {
                font-weight: var(--font-bold);
            }
        }
        span:last-child {
            display: flex;
            align-items: center;
        }
    }
    .collapse {
        @media (min-width: 992px) {
            height: auto !important;
            pointer-events: none;
        }
    }
    .collapse-wrapper {
        pointer-events: initial;
        cursor: initial;
        @media (min-width: 992px) {
            background: var(--second-bg-color);
            padding: 30px;
        }
        .description {
            margin-top: 16px;
            color: var(--body-color-2);
            @media (min-width: 992px) {
                margin-top: 0;
            }
            ul:not(:last-child) {
                margin-bottom: 20px;
            }
            p:last-child {
                margin-bottom: 0;
            }
            a {
                color: var(--main-color);
                &:hover {
                    @media (hover: hover) {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
} 
/* end product page */ 